export const TICK_RATE: number = 1000;
export const ICONS = ["fish", "poop", "weather"];
export const RAIN_CHANCE: number = 0.2;
export const SCENES = ["day", "rain"];
export const DAY_LENGTH: number = 60;
export const NIGHT_LENGTH: number = 3;

// TODO:
// (ISSUE)
// If timers are the same the game won't work.
// For instance if getNextPoopTime is the same as celebratingTime
// it will celebrate but never poop, and it will die because the poop
// was neve cleaned.
interface getTime {
  (clock: number): number;
}
export const getNextHungerTime: getTime = (clock) => {
  return Math.floor(Math.random() * 3) + 5 + clock;
};
export const getNextDieTime: getTime = (clock) => {
  return Math.floor(Math.random() * 2) + 3 + clock;
};
export const getNextPoopTime: getTime = (clock) => {
  return Math.floor(Math.random() * 2) + 6 + clock;
};

export const writeModal = (text: string = ""): void => {
  document.querySelector(
    ".modal"
  ).innerHTML = `<div class="modal-inner">${text}</div>`;
};
