import { ICONS } from "./constants";

const toggleHightlighted = (iconIndex: number, show: boolean) => {
  document
    .querySelector(`.${ICONS[iconIndex]}-icon`)
    .classList.toggle("highlighted", show);
};

export default function initButtons(handleUserAction: (icon: string) => void) {
  let selectedIcon = 0;

  function buttonClick({ target }) {
    if (target.classList.contains("left-btn")) {
      toggleHightlighted(selectedIcon, false);
      selectedIcon = (2 + selectedIcon) % ICONS.length;
      toggleHightlighted(selectedIcon, true);
    } else if (target.classList.contains("right-btn")) {
      toggleHightlighted(selectedIcon, false);
      selectedIcon = (1 + selectedIcon) % ICONS.length;
      toggleHightlighted(selectedIcon, true);
    } else {
      handleUserAction(ICONS[selectedIcon]);
    }
  }

  document.querySelector(".buttons").addEventListener("click", buttonClick);
}
